.Card {
    display: flex;
    flex-direction: row;
    padding: 5px;
    justify-content: flex-start;
    background-color: #219ebc;
    width: 80%;
    border-radius: 30px;
    align-items: center;
    margin-bottom: 15px;
    margin-top: 15px;
    border: 2px solid black;
    
    
    
}

.firstItem{
    margin: 1rem 0.8rem;
    border: 2px solid black;
    padding: 0.5rem 1.2rem;
    border-radius: 1rem;
    background-color: #8ecae6;

    
    
}

.secondItem{
    margin-left: 1rem;
    margin-right: auto;


}

.thirdItem{
    background-color: #8ecae6;
    padding: 10px 25px;
    border: 2px solid black;
    border-radius: 1rem;
    margin-right: 1.2rem;
}

